var $ = require('jquery');


//https://github.com/aui/artTemplate
var template = require('art-template');// 模版引擎使用

if(process.env.NODE_ENV !== 'production'){
	// http://mockjs.com/
	// 只有不是开发环境 才使用 mock模拟ajax
	require('mockData/demo.js');
}

// 使用 mockjs 模拟数据提交 例子
$.ajax({
	type:'post',
	url: '/list/mylist',
	dataType: 'json',
	success: function(data){
		var html = template.render(require('tpl/demo.vue').template)(data);

		$('#list').html(html);
	}
})



// 弹框框架
//http://layer.layui.com/
